.App {
  text-align: center;
}
.logo_wrapper{
  width: 25%;
  float: left;
  height: 100%;
}
.logo_wrapper div{
  height: 45px;
  width: 133px;
  background:url("./assets/NT_Logo.png") ;
  background-size: contain;
  margin-left:30px;
  background-repeat: no-repeat;
}
.link_wrapper{
  width: 75%;
  float: left;
  line-height: 3;
  height: 100%;
}
.link_wrapper nav{
  margin-right: 10%;
  text-align: right;
}
nav a {
  color: #00a9e2;
  font-family: "Humanst521 BT";
  font-size: 17px;
  margin-right: 30px;
  text-decoration: none !important;
  padding: 10px;
  background: #f9f9f9;
}
nav a.active{
  border-bottom: solid 1px;;
}
nav a:hover{
  border-bottom: solid 2px #00a9e2;
}
.nav_wrapper{
  height: 10%;
  padding: 10px;
  border-top: solid 3px #00a9e2;
  min-height: 65px;
}
.defColor{
  color: #146c8c;
}
.list-wrapper{
  font-family: "Humanst521 BT";
}
.list-wrapper a,button{
  float: right;
  margin-left: 3px;
}
.footer_wrapper{
  height: 5%;
  background:#142f39;;
  color: #00a9e2;
  width: 100%;
  font-size: 12px;
  text-align: center;
}
.footer_wrapper span{
  line-height: 2;
}
.content_wrapper{
  min-height: 85%;
  width: 100%;
  
  background: linear-gradient(
  rgba(1, 84, 109, 0.9),
  rgba(10, 112, 158, 0.9) )
  , url("./assets/nt-strategy.jpg");
  background-size: cover;
  background-repeat: no-repeat;
}
.form_wrapper{
  padding-top: 5%;
  font-family: "Humanst521 BT";
  font-size: 17px;
  color:#146c8c
}
.react-datepicker-wrapper{
  width: 100%;
}
.display-table header{
  display: none !important;
}
.rdt_TableHeadRow{
  background: #e7f3f7  !important;
}
.rdt_TableHeadRow  .iVrbmh{
  font-size: 16px !important;
  margin-left: 20px;
} 
.plusIcon{
  cursor: pointer;
}
.load-overlay{
  background: grey;
  position: absolute;
  width: 100%;
  opacity: .4;
  z-index: 1;
  min-height: 85%;
  text-align: center;
}
.mtopfix{
  margin-top: 16%;
}
.sub-section{
  padding: 3px;
  background: #f5f4f4;
}
.toast-container{
  position: absolute;
  width: 25% !important;
  z-index: 2;
}
.add-btn-container{
  width: 30%;
  padding: 6px;
  background: #e7fbff;
  border: solid 1px #d0e4ea;
  text-align: center;
  cursor: pointer;
}

.toast-header,.toast-body{
  font-size: 17px;
  font-family: "Humanst521 BT";
  
}
.saveBtn{
  background: #106079 !important;
  width:130px;
}
.global-filter-holder{
  text-align: right;
  width: 25% !important;
  font-family: "Humanst521 BT";
}

.export-btn{
  border:none;
}
.export-csv a{
  color: green !important;
}
.export-pdf{
  color: red;
}
@media screen and (max-width: 600px) {
  .global-filter-holder {
    width: 100% !important;
  }
  .logo_wrapper div{
    margin-left: 0 !important;
  }
  .add-btn-container{
    width: 100% !important;
  }
  .trashIcon{
    width: 100%;
    text-align: center;
  }
  .saveBtn{
    width: 100%;
  }
}